import { render } from '@testing-library/react';
import axios from 'axios';
import react, { Component, Fragment, useEffect, useState } from 'react';
import './imageSlider.css'
class Slider extends Component {
    
    state = {
        URLS: [],
        index: 0
    }

    componentDidMount(){
        axios.get('./ImageURL.json').then(res => this.setState({URLS: res.data.imageURLS}));
        setInterval(() => {
            if (this.state.index < this.state.URLS.length-1) {
            let index = this.state.index + 1
            this.setState({index })
            }else{
                this.setState({index: 0 })
            }
        }, 5000);
    }

    moveRight = (e) => {
        if (this.state.index < this.state.URLS.length-1) {
            let index = this.state.index + 1
            this.setState({index })
        }else{
            this.setState({index: 0 })
        }  
    }

    moveleft = () => {
        if (this.state.index > 0 ) {
            let index = this.state.index - 1
            this.setState({index })
        }else{
            this.setState({index: this.state.giURLS.length-1 })
        }
    }

    render(){
        console.log(this.state.URLS);
        const Images = this.state.URLS.map( (url , index) => {
            return (
                <img key={index} src={url} />
                )
            });
        let theImage = Images[this.state.index];

        return(
            <Fragment>
                <div className="container">
                    <span onClick={this.moveleft}> <i className="fas fa-caret-left"></i> </span>
                    <span onClick={this.moveRight}> <i className="fas fa-caret-right"></i> </span>
                    {theImage}
                </div>
            </Fragment>
            )
    }
}

export default Slider ;